@import url('https://fonts.googleapis.com/css?family=Dancing+Script|Raleway&display=swap');
.containerFull {
  background: linear-gradient(to right, #33ccff 0%, #ff99cc 100%);
}
.containerHome {
  max-width: 1350px;
  margin: auto;
}
.imageGallery {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.cursiveText {
  font-family: 'Raleway', sans-serif;
}
* {
  font-family: 'Dancing Script', cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.containerTwo {
  display: flex;
  justify-content: space-around;
  .like {
    color: red;
  }
  .photoIcon {
    color: blue;
  }
}
.container3 {
  .new {
    display: flex;
    justify-content: center;
    .thumbUp {
      margin-right: 2rem;
      color: #34d6c9;
    }
  }
  h2 {
    padding: 2rem;
    text-align: center;
    color: white;
  }
  p {
    text-align: center;
  }
}
@media (min-width: 768px) {
  .containerHome {
    display: flex;
    max-width: 1350px;
    margin: auto;
    .imageGallery {
      width: 30%;
    }
    .containerTwo {
      width: 30%;
    }
    .container3 {
      width: 30%;
    }
  }
  // .containerTwo {
  //   justify-content: flex-start;
  // }
}
