@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Raleway&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.containerFull{background:linear-gradient(to right, #3cf 0%, #f9c 100%)}.containerHome{max-width:1350px;margin:auto}.imageGallery{display:block;margin-left:auto;margin-right:auto}.cursiveText{font-family:'Raleway', sans-serif}*{font-family:'Dancing Script', cursive;margin:0;padding:0;box-sizing:border-box}.containerTwo{display:flex;justify-content:space-around}.containerTwo .like{color:red}.containerTwo .photoIcon{color:blue}.container3 .new{display:flex;justify-content:center}.container3 .new .thumbUp{margin-right:2rem;color:#34d6c9}.container3 h2{padding:2rem;text-align:center;color:white}.container3 p{text-align:center}@media (min-width: 768px){.containerHome{display:flex;max-width:1350px;margin:auto}.containerHome .imageGallery{width:30%}.containerHome .containerTwo{width:30%}.containerHome .container3{width:30%}}

